import Vue from 'vue'
import vueRouter from 'vue-router'

Vue.use(vueRouter)
import Home from '@/pages/home'
import About from '@/pages/about'
import Teach from '@/pages/Teaching'
import Project from '@/pages/Project'
export default new vueRouter({
    routes: [
        {
            path: '/home',
            component: Home,
        },
        {
            path: '/about',
            component: About,
        },
        {
            path: '*',
            redirect: '/home'
        },
        {
            path: '/teach',
            component: Teach,
        },
        {
            path: '/project',
            component: Project,
        }
    ]
})